.root{
    height:.79rem;
    line-height: .8rem;
    border-bottom: 1px solid #273c7b;
}
.nav-logo{
    display: inline-block;
    height:.8rem;
    margin-left: 50px;
    margin-right: 160px;
}
.logo{
    width:.4rem;
    height: .32rem;
    vertical-align: middle;
}
.name{
    margin-left: 10px;
    color:#fff;
    font-size: .3rem;
    vertical-align: middle;
}
.nav-links{
    height:.8rem;
}
.nav-links .item{
    display: inline-block;
    padding: 0 .3rem;
    height: .8rem;
    text-decoration: none;
    color: #fff;
    font-size: 0.18rem;
}
.nav-btns{
    height:.8rem;
}
.btn{
    display: inline-block;
    height: .8rem;
    padding: 0 .2rem;
    color:#fff;
    font-size:.18rem;
}
.btn-active{
    composes: btn;
    background: #1B82F5;
}


