.root{
    margin-top: .1rem;
    font-size: 0;
}
.top-area{
    width:12rem;
    margin: 0 auto;
}
.bottom-area{
    background: #307c9e;
}
.bottom-area .content{
    width:12rem;
    margin: 0 auto;
}
.title{
    margin:.7rem 0 .1rem;
    color: #fff;
    font-size: .6rem;
}
.tips{
    margin:.2rem 0;
    color:#4188a8;
    font-size:.22rem;
}
.banner{
    width:5rem;
    height: 4.3rem;
}
.icon-block{
    display: inline-block;
    height: 1.2rem;
    line-height: 1.2rem;
}
.icon{
    width:.72rem;
    height: .72rem;
    margin:0 .3rem 0 .4rem;
    vertical-align: middle;
}
.icon-tips{
    display: inline-block;
    margin-right: .6rem;
    color:#fff;
    font-size: .24rem;
    vertical-align: middle;
}


